<template>
  <div class="admin-manage">
    <div class="left">
      <div class="top">
        <Button v-if="adminInfo.role === 1" @click="addShow = true" type="warning">添加管理员账号</Button>
      </div>
      <div class="admin-list">
        <Input @on-search="getAdminList" style="margin-bottom: 1rem;" search
               placeholder="搜索管理员（账号、昵称）"/>
        <ul>
          <li @click="adminSelect(item, key)" v-for="(item, key) in adminList" :key="item.id"
              :class="[selectIndex === key ? 'select' : '']">
            <div class="top">
              <Tag color="primary">{{
                  item.role === 1 ? '超级管理员' : '管理员'
                }}</Tag>
            </div>
            <div class="middle">
              <Tooltip theme="light" :content="item.admin">
                <Avatar class="img">{{item.admin}}</Avatar>
              </Tooltip>
            </div>
            <div class="bottom ellipsis">
              {{ item.username }}
            </div>
          </li>
        </ul>
        <div class="no-thing" style="width: 100%;line-height: 5rem;display: flex;justify-content: center">
          <span>{{ adminList.length === 0 ? '没有找到匹配的管理员哟' : '到底了' }}</span>
        </div>
      </div>
    </div>
    <div class="right">
      <!--添加管理员-->
      <section v-if="addShow" class="add-admin">
        <Tabs value="name1">
          <TabPane label="添加管理员" name="name1">
            <ul class="ul" v-if="adminInfo.role === 1">
              <li>
                <div class="left">管理员账号：</div>
                <div class="right">
                  <Input maxlength="20" v-model="admin" placeholder="请设置管理员登录账号"
                         style="width: 30rem"/>
                </div>
              </li>
              <li>
                <div class="left">管理员昵称：</div>
                <div class="right">
                  <Input maxlength="20" v-model="username" placeholder="请输入管理员昵称"
                         style="width: 30rem"/>
                </div>
              </li>
              <li>
                <div class="left">管理员性别：</div>
                <div class="right">
                  <el-radio v-model="gender" :label="1">男</el-radio>
                  <el-radio v-model="gender" :label="0">女</el-radio>
                </div>
              </li>
              <li>
                <div class="left">角色：</div>
                <div class="right">
                  <el-radio v-model="role" :label="1">超级管理员</el-radio>
                  <el-radio v-model="role" :label="2">管理员</el-radio>
                </div>
              </li>
              <li>
                <div class="left">账号密码：</div>
                <div class="right">
                  <Input v-model="password" placeholder="请设置密码" type="password" style="width: 30rem"/>
                </div>
              </li>
              <li>
                <div class="left"></div>
                <div class="right">
                  <Button @click="addAdmin" type="primary">添加管理员账号</Button>
                </div>
              </li>
            </ul>
            <div style="width: 100%;height: 30rem;display: flex;justify-content: center;align-items: center" v-else>
              暂无权限
            </div>
          </TabPane>
        </Tabs>
      </section>
      <!--管理员发布的课程-->
      <section class="teacher-detail">
        <div v-if="JSON.stringify(selectAdminInfo) !== '{}'" class="admin-info">
          <Tabs value="name1">
            <TabPane label="基本信息" name="name1">
              <ul class="ul">
                <li>
                  <div class="left">管理员账号：</div>
                  <div class="right">
                    {{ selectAdminInfo.admin }}
                  </div>
                </li>
                <li>
                  <div class="left">管理员昵称：</div>
                  <div class="right">
                    <Input :disabled="adminInfo.role === 2" maxlength="20" v-model="selectAdminInfo.username" placeholder="请输入管理员昵称"
                           style="width: 30rem"/>
                  </div>
                </li>
                <li>
                  <div class="left">管理员性别：</div>
                  <div class="right">
                    <el-radio :disabled="adminInfo.role === 2" v-model="selectAdminInfo.gender" :label="1">男</el-radio>
                    <el-radio :disabled="adminInfo.role === 2" v-model="selectAdminInfo.gender" :label="0">女</el-radio>
                  </div>
                </li>
                <li>
                  <div class="left">角色：</div>
                  <div class="right">
                    <el-radio :disabled="adminInfo.role === 2" v-model="selectAdminInfo.role" :label="1">超级管理员</el-radio>
                    <el-radio :disabled="adminInfo.role === 2" v-model="selectAdminInfo.role" :label="2">管理员</el-radio>
                  </div>
                </li>
                <li>
                  <div class="left"></div>
                  <div class="right">
                    <Button v-if="adminInfo.role === 1" @click="saveAdminInfo" type="primary">修改信息</Button>
                    <Button v-if="adminInfo.role === 1" style="margin-left: 1rem;" @click="delAdmin"
                            type="error">删除
                    </Button>
                  </div>
                </li>
              </ul>
            </TabPane>
          </Tabs>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import {addAdmin, getAdminList, saveAdminInfo, delAdmin} from "../../../api";
import {mapState} from 'vuex'
import {check_name} from "@/common/js/myFun";

export default {
  name: "adminManage",
  data() {
    return {
      admin: '',
      username: '',
      gender: 1,
      role: 2,
      password: '',
      keyWord: '',
      adminList: [],
      addShow: true,
      selectIndex: -1,
      selectAdminInfo: {},
      courseList: []
    }
  },
  computed: {
    ...mapState(['adminInfo'])
  },
  mounted() {
    this.getAdminList();
  },
  methods: {
    async delAdmin() {
      this.$Modal.confirm({
        title: '删除管理员',
        content: '确认删除此管理员吗？',
        okText: '删除',
        onOk: async () => {
          let data = await delAdmin({
            id: this.selectAdminInfo.id
          });
          if (data.code === 200) {
            this.adminList.splice(this.selectIndex, 1);
            this.$Message.success(data.data);
            this.addShow = true;
            this.selectIndex = -1;
            this.selectAdminInfo = {};
          } else {
            this.$Message.error(data.data);
          }
        }
      });
    },
    async saveAdminInfo() {
      let {username, gender, id, role} = this.selectAdminInfo;
      if (!check_name(username)) {
        return this.$Message.warning('管理员昵称格式错误');
      }
      let data = await saveAdminInfo({
        reviseUsername: username, reviseGender: gender, id, reviseRole: role
      });
      if (data.code === 200) {
        this.$Message.success(data.data);
        this.getAdminList();
      } else {
        this.$Message.error(data.data);
      }
    },
    adminSelect(admin, key) {
      // eslint-disable-next-line no-console
      console.log(admin);
      this.addShow = false;
      this.selectIndex = key;
      this.selectAdminInfo = JSON.parse(JSON.stringify(admin));
    },
    async getAdminList(keyWord = '') {
      let data = await getAdminList({
        keyword: keyWord
      });
      if (data.code === 200) {
        let {adminList} = data.result;
        this.adminList = adminList;
      } else {
        this.$Message.warning(data.data);
      }
    },
    async addAdmin() {
      let {admin, username, gender, password, role} = this;
      if (!admin) {
        return this.$Message.warning('管理员账号不得为空');
      }
      if (!check_name(username)) {
        return this.$Message.warning('管理员昵称格式错误');
      }
      if (password.length < 6) {
        return this.$Message.warning('密码长度不得小于6位');
      }
      let data = await addAdmin({
        admin, username, gender, password, role
      });
      if (data.code === 200) {
        this.admin = '';
        this.username = '';
        this.password = '';
        this.$Message.success(data.data);
        this.getAdminList();
      } else {
        this.$Message.error(data.data);
      }
    },
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
.course-list {
  width: 100%
  display flex
  flex-wrap wrap

  > li {
    width: 30rem
    height: 13rem
    border-bottom .1rem solid #f9f9f9
    cursor pointer
    padding: .5rem
    position relative
    overflow hidden
    transition all .3s
    margin: 1rem

    &:hover {
      > .left {
        filter: blur(.07rem);
        -webkit-filter: blur(.07rem);
        -moz-filter: blur(.07rem);
        -ms-filter: blur(.07rem);
        -o-filter: blur(.07rem);
      }
    }

    &.select {
      > .left {
        filter: blur(0rem);
        -webkit-filter: blur(0rem);
        -moz-filter: blur(0rem);
        -ms-filter: blur(0rem);
        -o-filter: blur(0rem);
      }
    }

    > .left {
      width: 100%
      height: 100%
      transition all .3s

      > img {
        width: 100%
        height: 100%
        object-fit cover
      }
      filter: blur(.15rem);
      -webkit-filter: blur(.15rem);
      -moz-filter: blur(.15rem);
      -ms-filter: blur(.15rem);
      -o-filter: blur(.15rem);
    }

    > .right {
      position absolute
      left: 0
      top: 0
      width: 100%
      height: 100%
      padding: 1rem
      color white
      display flex
      align-items center
      justify-content center

      > .title {
        width: 100%
        font-weight: bold;
        text-align center
      }

      > .bottom {
        width: 100%
        position absolute
        bottom: 0
        left: 0
        padding: 1.2rem
        text-align right
        font-size: 1.2rem
        color #ebebeb
      }

      > .top {
        width: 100%
        position absolute
        left: 0
        top: 0
        padding: 1.2rem

        > .left {
          float left
        }

        > .right {
          float right
        }
      }
    }
  }
}

.admin-manage {
  width: 100%
  height: 100%
  overflow: hidden;
  display flex

  > .left {
    flex 4
    max-width 30rem
    min-height 30rem
    height: 100%
    background-color: #fff
    overflow auto
    padding: 1rem

    > .top {
      width: 100%
      height: 5rem
      display flex
      align-items center
      justify-content center
    }

    > .admin-list {
      width: 100%

      > ul {
        margin-top: 2rem
        width: 100%
        display flex
        flex-wrap wrap

        > li {
          width: 33%
          padding: .5rem
          height: 9em
          cursor pointer
          background-color: #fff
          transition all .3s

          &:hover {
            background-color: #f9f9f9
          }

          &.select {
            background-color: #f1f1f1
          }

          > .top {
            width: 100%
            display flex
            justify-content center
          }

          > .middle {
            width: 100%
            display flex
            align-items center
            justify-content center
            margin-top: .3rem

            .img {
              border-radius 50%
              width: 6rem
              height: 6rem
              display flex
              justify-content center
              align-items center
            }
          }

          > .bottom {
            width: 100%
            display flex
            align-items center
            justify-content center
            line-height: 3rem
            font-size: 1.2rem
            font-weight: bold;
          }
        }
      }
    }
  }

  > .right {
    flex 6
    height: 100%
    overflow auto
    background-color: #f9f9f9
    padding: 1rem

    > .teacher-detail {
      width: 100%

      > .admin-info {

      }
    }
  }
}
</style>
